import '../App.css';
import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col} from 'react-bootstrap';
import Portallist from './portallist';
import Mychart from './mychart'
import Mylistcount from './mylistcount';
import Napgichart from './napgichart';


function Boardservice() {
    return(
        <div>
            <Container>
                <Row>
                    <Col md="7" className='th_ser'><span className='ser_bar'>|</span>   </Col>
                    <Col md="5" className='th_ser'><span className='ser_bar'>|</span>  </Col>
                </Row>
                <Row>              
                    <Col md="7"><Portallist/> </Col>
                    <Col md="5"><Mylistcount /></Col>      
                </Row>
                <Row>              
                    <Col md="12"></Col>     
                </Row>
                <Row className='chart_margin_top'>
                    <Col md="7" className='th_ser'><span className='ser_bar'>|</span>  </Col>
                    <Col md="5" className='th_ser'><span className='ser_bar'>|</span>  SLA  </Col>
                </Row>
                <Row>
                    <Col md="7"><Mychart /></Col>
                    <Col md="5"><Napgichart /></Col>
                </Row>
            </Container>
        </div>
    )
};

export default Boardservice;
