import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import data from '../data/count.json';

function Mylistcount()
{
    return(
        <div>
            <table className='table3'>
                <tr>
                    <td colSpan={7}><h6 className='l_title'>{data[2].title}</h6></td>
                </tr>
                <tr>
                    <td colSpan={7}><h4 className='h4_t'>{data[2].col1}</h4></td>
                </tr>
                <tr>
                    <td></td>
                    <td><span>|</span></td>
                    <td>7 </td>
                    <td><span>|</span></td>
                    <td>3 </td>
                    <td><span>|</span></td>
                    <td> </td>
                </tr>
                <tr>
                    <td><span className='text_color1'>{data[2].col2}</span></td>
                    <td><span></span></td>
                    <td><span className='text_color2'>{data[2].col3}</span></td>
                    <td><span></span></td>
                    <td><span className='text_color3'>{data[2].col4}</span></td>
                    <td><span></span></td>
                    <td><span className='text_color4'>{data[2].col5}</span></td>
                </tr>
            </table>
            

            <table className='table4'>
                <tr>
                    <td colSpan={7}><h6 className='l_title'>{data[3].title}</h6></td>
                </tr>
                <tr>
                    <td colSpan={7}><h4 className='h4_t'>{data[3].col1}</h4></td>
                </tr>
                <tr>
                    <td></td>
                    <td><span>|</span></td>
                    <td></td>
                    <td><span>|</span></td>
                    <td></td>
                    <td><span>|</span></td>
                    <td></td>
                </tr>
                <tr>
                    <td><span className='text_color1'>{data[3].col2}</span></td>
                    <td><span></span></td>
                    <td><span className='text_color2'>{data[3].col3}</span></td>
                    <td><span></span></td>
                    <td><span className='text_color3'>{data[3].col4}</span></td>
                    <td><span></span></td>
                    <td><span className='text_color4'>{data[3].col5}</span></td>
                </tr>
            </table>
        </div>
    )
};

export default Mylistcount;