import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import data from '../data/count.json';

function CountComponent()
{
    return(
        <div>
             <table className='table1'>
                  <tr>
                    <td colSpan={7}><h6 className='l_title'>{data[0].title}</h6></td>
                  </tr>
                  <tr>
                    <td colSpan={7}><h4 className='h4_t'>{data[0].col1}</h4></td>
                  </tr>
                  <tr>
                    <td><span className='text_color1'>{data[0].col2}</span></td>
                    <td><span>|</span></td>
                    <td><span className='text_color2'>{data[0].col3}</span></td>
                    <td><span>|</span></td>
                    <td><span className='text_color3'>{data[0].col4}</span></td>
                    <td><span>|</span></td>
                    <td><span className='text_color4'>{data[0].col5}</span></td>
                  </tr>
                </table>

                <table className='table2'>
                  <tr>
                    <td colSpan={7}><h6 className='l_title'>{data[1].title}</h6></td>
                  </tr>
                  <tr>
                    <td colSpan={7}><h4 className='h4_t'>{data[1].col1}</h4></td>
                  </tr>
                  <tr className='text_sub'>
                    <td><span className='text_color1'>{data[1].col2}</span></td>
                    <td><span className='bar'>|</span></td>
                    <td><span className='text_color2'>{data[1].col3}</span></td>
                    <td><span className='bar'>|</span></td>
                    <td><span className='text_color3'>{data[1].col4}</span></td>
                    <td><span className='bar'>|</span></td>
                    <td><span className='text_color4'>{data[1].col5}</span></td>
                  </tr>
                </table>
        </div>
    )
}

export default CountComponent;