import { useState, useEffect } from 'react';
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Routes, Route} from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import { Container, Row, Col } from 'react-bootstrap';
import Paging2 from './paging2';
import Detail from './detail';
import axios from 'axios'
import Form from 'react-bootstrap/Form';


function Incident() {
 //   . DB     
 let [data, setData] = useState([]);
 useEffect(()=>{
  (async () =>{
  axios.get('https://violetleaf44.github.io/react/data/indata.json').then((response)=>{
     setData(response.data);
      })
      .catch(()=>{
        console.log('failed')
      });
    }
  )();
},[])
 //   

 const [search, setSearch] = useState("");
 const [search2, setSearch2] = useState("");
 const numChange = (e) => {
   setSearch(e.target.value);
   console.log(e.target.value);
 }

 const nameChange = (e) => {
   console.log(e.target.value);
 }
 const titleChange = (e) => {
   console.log(e.target.value);
 }
 const OPTIONS = [
   { value: "", name: "-------------------------" },
   { value: "", name: "" },
   { value: "", name: "" },
   { value: "", name: "" },
 ];

 const SelectBox = (props) => {
   const typeChange = (e) => {
     setSearch2(e.target.value);
     console.log(e.target.value);
   };

   return (
     <select onChange={typeChange}>
       {props.options.map((option) => (
         <option
           key={option.value}
           value={option.value}
           defaultValue={props.defaultValue === option.value}
         >
           {option.name}
         </option>
       ))}
     </select>
   );
 };


 return (
   <div>
     <Container className='sr_table'>
       {/*  */}
       <Row>
         <Col className='th_ser'><span className='ser_bar'>|</span> </Col>
       </Row>
       <Row className='sr_text'>
         <Col md="1"></Col>
         <Col md="3">
           <Form.Group className="mb-3">
             <Form.Control placeholder="Disabled input" disabled />
           </Form.Group>
         </Col>
         <Col md="1"></Col>
         <Col md="3">
          <Form.Group className="mb-3">
            <Form.Select id="srType" >
              <option value=""></option>
              <option value=""></option>
              <option value="OA">OA</option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value="">--  --</option>
            </Form.Select>
           </Form.Group>
           </Col>
         <Col md="1"></Col>
         <Col md="3">
         <Form.Group className="mb-3">
             <Form.Control placeholder="Disabled input" disabled />
           </Form.Group>
          
         </Col>
       </Row>
       <Row className='sr_text'>
         <Col md="1"></Col>
         <Col md="11">
           <Form.Group className="mb-3">
             <Form.Control placeholder="Disabled input" disabled />
           </Form.Group>
         </Col>
       </Row>
       <Row className='sr_text'>
         <Col md="1"></Col>
         <Col md="3">
          <Form.Group className="mb-3">
           <Form.Select id="srState">
             <option value=""></option>
             <option value=""></option>
             <option value=""></option>
             <option value=""></option>
             <option value=""></option>
             <option value=""></option>
             <option value="">--  --</option>
           </Form.Select>
           </Form.Group>
          </Col>
         <Col md="1"></Col>
         <Col md="3">
         <Form.Group className="mb-3">
           <Form.Control placeholder="Disabled input" disabled />
         </Form.Group>
         </Col>
         <Col md="1"></Col>
         <Col md="3">
         <Form.Group className="mb-3">
           <Form.Control placeholder="Disabled input" disabled />
         </Form.Group>
         </Col>
         <Col md="4"></Col>
       </Row>
       <Row>
         <Col md="4"></Col>
         <Col md="4" className='btn_inbox'><Button variant="primary"></Button></Col>
         <Col md="4"></Col>
       </Row>
       <Row className='row_line'>
       <Col className='th_ser'><span className='ser_bar'>|</span> </Col>
       </Row>
     </Container>
     {/*   */}

     {/*   */}
     <Routes>
       <Route path="/" element={<Paging2 data={data} />} />;
       <Route path="/detail/:id" element={<Detail data={data} />} />;
       <Route path="*" element={<div>404  .</div>} />
     </Routes>
     {/*   */}

   </div>
 );
}

