import React, { useEffect, useState } from "react";
import '../App.css';
import Pagination from "react-js-pagination";
import { useNavigate} from 'react-router-dom';
import { Container, Row, Col } from 'react-bootstrap';
import axios from 'axios'

const Paging2 = () => {
  let [data,setData] = useState([]);
  useEffect(()=>{
    (async () =>{
    axios.get('https://violetleaf44.github.io/react/data/indata.json').then((response)=>{
       console.log(response.data);
       setData(response.data);
        })
        .catch(()=>{
          console.log('failed')
        });
      }
    )();
  },[])
 

  const [page, setPage] = useState(1);
  const [items, setItems] = useState(20);

  const handlePageChange = (page) => { setPage(page); };
  const itemChange = (e) => {
    setItems(Number(e.target.value))

  }
  // console.log(items*(page-1), items*(page-1)+items);
  console.log("2");

  let navigate = useNavigate();

  return (
    <div>
      {/* <div>
        <select name="items" onChange={itemChange}>
          <option value="5">5</option>
          <option value="10">10</option>
          <option value="15">15</option>
          <option value="20">20</option>
        </select>
      </div> */}
     

      <Container>
             
      <Row className="paging_box">
          <Col className="paging_text_right" md="1"></Col>
          <Col className="paging_text_right" md="1"></Col>
          <Col className="paging_text_right" md="1"></Col>
          <Col md="4"></Col>
          <Col className="paging_text_right" md="1"></Col>
          <Col className="paging_text_center" md="2"></Col>
          <Col className="paging_text_center" md="2"></Col>
        </Row>
         
      {data.slice(10*(page-1), 10*(page-1)+10
      ).map((v,i) => {
        return (
          <Row className="paging_text" onClick={()=>{navigate('/incident/detail/'+ v.id)}}>
            <Col md="1">{v.sr_id}</Col>
            <Col md="1">{v.type}</Col>
            <Col md="1">{v.name}</Col>
            <Col className="paging_text_left" md="4">{v.title}</Col>
            <Col md="1">{v.state}</Col>
            <Col className="paging_text_center" md="2">{v.startday}</Col>
            <Col className="paging_text_center" md="2">{v.endday}</Col>
        </Row>
        )
      })}
      </Container>

      <Pagination
        activePage={page}
        itemsCountPerPage={10}
        totalItemsCount={120}
        pageRangeDisplayed={5}
        prevPageText={""}
        nextPageText={""}
        onChange={handlePageChange} 
        />
    </div>
  )
};

export default Paging2;
