
import data1 from '../data/srdata'
import { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col} from 'react-bootstrap';
import '../App.css';
import { Routes, Route, useNavigate } from 'react-router-dom';

function Portallist() {
    let [data] = useState(data1);
    
  return (
    <div>
      <Container>
      <Row className='service_list'>
            <Col md="3">SR</Col>
            <Col md="3"></Col>
            <Col md="6"></Col>
        </Row>
          
         {
              data.map(function(a,i)
              {
                if(i<12){
                  return(
                    <DataMap data={data} i={i} key={i}/>
                  )
                } 
                return null;
              })
          }
      </Container>
    </div>
  )
}

function DataMap(props){
  let i = props.i;
  let navigate = useNavigate();
  return(
          <Row>
            <Col md="3">{props.data[i].sr_id}</Col>
            <Col md="3">{props.data[i].type}</Col>
            <Col md="6" className='service_list_td'>{props.data[i].title}</Col>
          </Row>
          )
}

export default Portallist