
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import book from '../book.png';
import air from '../air.png';
import glass from '../glass.png';
import Portaltab from './portaltab';
import CountComponent from './countcomponent';

function Mainportal()
{
    return(
        <div className='back_box'>
        <div className='main_back'></div>
            <div className='main_btn'>
              <div className='btn_box'>
                <div className='service'>
                  <h5> </h5>
                  <img className='icon' src={book} />
                </div>
                <div className='service'>
                  <h5></h5>
                  <img className='icon' src={air} />
                </div>
                <div className='service'>
                  <h5></h5>
                  <img className='icon' src={glass} />
                </div>
              </div>
            </div>

            <div className='content'>
              <div className='content_L'>
                <CountComponent />
              </div>

              <div className='content_R'>
                <Portaltab />
              </div>
            </div>
            </div>
    )
}

export default Mainportal